body {
  font-family: 'Open Sans', sans-serif;
  background-color: #2A2B2E;
  color: #ffffff;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  text-align: center;
  background-color: #1E1F22;
  padding: 40px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 500px;
}

label {
  font-weight: bold;
  display: block;
  margin-bottom: 10px;
}

input[type="text"] {
  width: 100%;
  padding: 12px;
  margin-bottom: 20px;
  border: 1px solid #444;
  border-radius: 4px;
  background-color: #333;
  color: #fff;
}

button {
  padding: 12px 24px;
  background-color: #E84393;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #D63074;
}

button:disabled {
  background-color: #888;
  cursor: not-allowed;
}

.results-container {
  margin-top: 20px;
  text-align: left;
}

.file-links ul,
.result-data ul {
  list-style-type: none;
  padding: 0;
}

.file-links li,
.result-data li {
  margin-bottom: 5px;
  color: #E84393;
}

.file-links li a,
.result-data li a {
  color: #E84393;
  text-decoration: none;
  font-weight: bold;
}

.file-links li a:hover,
.result-data li a:hover {
  text-decoration: underline;
}

.checkmark {
  color: green;
}

.loading {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 2px solid #ccc;
  border-top-color: #007BFF;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
